import {requestGoodsCount,requestGoodsList} from '../../utils/request';

const state = {
  list:[],
  size:2,
  page:1,
  count:0
}

const mutations = {
  changeList(state,arr){
    state.list = arr
  },
  changeCount(state,n){
    state.count = n
  },
  changePage(state,page){
    state.page = page
  }
}

const actions = {
  listActions(context,value){
    var params = null;
    if(value !== null){
      params = {
        page:context.state.page,
        size:context.state.size
      }
    }

    requestGoodsList(params).then(res=>{
      if(res.data.code == 200){
        context.commit('changeList',res.data.list);
      }
    })
  },
  countActions(context){
    requestGoodsCount().then(res=>{
      context.commit("changeCount",res.data.list[0].total)
    })
  },
  pageActions(context,page){
    context.commit("changePage",page)
  }
}


const getters = {
  list(state){
    return state.list
  },
  size(state){
    return state.size
  },
  count(state){
    return state.count
  }
}

export default{
  state,
  mutations,
  actions,
  getters,
  namespaced:true
}



