import {requestSpecsCount,requestSpecsList} from '../../utils/request';
const state = {
  list:[],
  size:2,//每页显示的条数
  page:1,//当前页码数
  count:0,//总的记录条数
}

const mutations = {
  changeList(state,arr){
    state.list = arr
  },
  changeCount(state,n){
    state.count = n;
  },
  changePage(state,page){
    state.page = page
  }
}

const actions = {
  listActions(context){
    var params = {
      size:context.state.size,
      page:context.state.page
    }
    requestSpecsList(params).then(res=>{
      if(res.data.code == 200){
        if((res.data.list == null || res.data.list.length == 0) && context.state.page > 1){
          var page = context.state.page - 1;
          context.commit('changePage',page);
          context.dispatch('listActions');
          return
        }
        context.commit('changeList',res.data.list)
      }
    })
  },
  countActions(context){
    requestSpecsCount().then(res=>{
      if(res.data.code == 200){
        context.commit('changeCount',res.data.list[0].total)
      }
    })
  },
  pageActions(context,page){
    context.commit('changePage',page)
  }
}


const getters = {
  list(state){
    return state.list
  },
  size(state){
    return state.size
  },
  count(state){
    return state.count
  }
}

export default {
  state,
  mutations,
  actions,
  getters,
  namespaced:true
}




